import React from 'react'
import { useNavigate } from 'react-router-dom';
import FAQs from '../components/FAQs';
import Messages from '../components/Messages';
import UserInfo from '../components/UserInfo';

function Home() {
  const [user, setUser] = React.useState();
  let navigate = useNavigate();
  React.useEffect(() => {
    document.title = 'Inbox | Pigeon';
    (window.adsbygoogle = window.adsbygoogle || []).push({});
    //console.log(localStorage.getItem('token'));
    if (localStorage.getItem('token') === null) {
      navigate('/create');
    } else {
      setUser(JSON.parse(localStorage.getItem('user')))
    }
  }, [navigate]);
  return (
    <div>

      <UserInfo id={user?._id} password={user?.password} />
      <ins class="adsbygoogle"
        style={{ display: "block", maxWidth: "700px", margin: "0 auto" }}
        data-ad-client="ca-pub-9587469013682576"
        data-ad-slot="9278221111"
        data-ad-format="auto"
        data-full-width-responsive="true"></ins>
      <Messages />
      <ins className="adsbygoogle"
        style={{ display: "block", maxWidth: "700px", margin: "0 auto" }}
        data-ad-client="ca-pub-9587469013682576"
        data-ad-slot="7226772843"
        data-ad-format="auto"
        data-full-width-responsive="true"></ins>
      <FAQs />
    </div >
  )
}

export default Home
